import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

import Card from './Card';
import RightArrowIcon from './icons/RightArrow';
import { screen } from '../constants';

export default function BlockCards({
  title = '',
  subtitle = '',
  cards = [],
  hideRightArrow = false,
  marginBottom = false,
}) {
  const data = useStaticQuery(graphql`
    {
      image: file(name: { eq: "card-banner" }) {
        childImageSharp {
          fluid {
            originalImg
          }
        }
      }
    }
  `);

  return (
    <div>
      {title && (
        <h4 className="block-3-card__pretitle app__pretitle">{title}</h4>
      )}
      {subtitle && (
        <h1 className="block-3-card__subtitle app__subtitle">{subtitle}</h1>
      )}
      <div
        className={`block-footer-gray-why_cards${
          marginBottom ? ' block-footer-gray-why_cards-margin-bottom' : ''
        }`}
      >
        {cards.map(card => (
          <Card
            key={card.id}
            title={
              <span className="block-footer-gray-why__title">
                {card.title}{' '}
                {!hideRightArrow && <RightArrowIcon color="#1f325d" />}
              </span>
            }
            href={card.href}
            hideLearnMore
            className="block-footer-gray-why_card"
          >
            {card.content}
          </Card>
        ))}
      </div>
      <style jsx>{`
        .block-3-card__pretitle {
          text-align: center;
        }

        .block-3-card__subtitle {
          text-align: center;
        }

        .block-footer-gray-why_cards {
          display: flex;
          width: 100%;
          justify-content: center;
        }

        .block-footer-gray-why_cards-margin-bottom {
          margin-bottom: -250px;
        }

        .block-footer-gray-why__title {
          display: flex;
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-footer-gray-why_cards {
            flex-direction: column;
            align-items: center;
          }
        }
      `}</style>
      <style jsx global>{`
        .block-footer-gray-why_card {
          background-image: url('${data.image.childImageSharp.fluid.originalImg}');
          background-size: 200px;
          background-repeat: no-repeat;
          background-position: right bottom;
          margin-right: 20px;
          background-color: white;
          width: 400px !important;
          min-height: 150px !important;
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-footer-gray-why_card {
            margin: 0 0 20px 0;
          }
        }
      `}</style>
      <style jsx global>{`
        .block-3-card__card {
          margin-right: 40px;
        }
        .block-3-card__card:last-child {
          margin-right: 0;
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-3-card__card {
            margin: 0 0 40px 0;
          }
          .block-3-card__card:last-child {
            margin-bottom: 0;
          }
        }
      `}</style>
    </div>
  );
}
