import React from 'react';
import Img from 'gatsby-image';
import { navigate } from 'gatsby';

import { screen } from '../constants';
import Button from './Button';

export default function BlockImageLeft({
  pretitle,
  title,
  description,
  quote,
  author,
  image,
  image_alt,
  sub_image,
  sub_image_alt,
  ctaText,
  ctaButtonTo,
}) {
  function handleCtaButtonTo() {
    navigate(ctaButtonTo);
  }
  return (
    <div className="block-image-left">
      <div className="app__image-group">
        <div className="app__image block-image-left__image">
          <Img fluid={image} alt={image_alt} />
        </div>
        {sub_image && (
          <div className="app__subimage block-image-left__subimage">
            <Img fluid={sub_image} alt={sub_image_alt} />
          </div>
        )}
      </div>
      <div className="block-image-left_text">
        <div className="app__pretitle">{pretitle}</div>
        <h2 className="app__title">{title}</h2>
        <div className="block-image-left_desc">{description}</div>
        <hr className="block-image-left__separator" />
        {quote && <p className="app__desc block-image-left_quote">{quote}</p>}
        {author && <p className="block-image-left_author">{author}</p>}
        {ctaButtonTo && (
          <Button large onClick={handleCtaButtonTo}>
            {ctaText}
          </Button>
        )}
      </div>
      <style jsx>{`
        .block-image-left {
          display: flex;
          justify-content: space-between;
          align-items: flex-start;
          margin-top: 64px;
        }

        .block-image-left_desc {
          font-size: 24px;
          line-height: 1.42;
          letter-spacing: 0.9px;
          margin-bottom: 45px;
        }

        .block-image-left_text {
          max-width: 560px;
          margin-left: 70px;
        }

        .block-image-left__separator {
          color: #8f99ae;
          margin-bottom: 45px;
        }

        .block-image-left_quote {
          max-width: 500px;
          font-size: 30px;
          line-height: 1.29;
          letter-spacing: 0px;
          margin-bottom: 45px;
        }

        .block-image-left_author {
          font-size: 22px;
          line-height: 1.64;
          letter-spacing: 0.8px;
        }

        .block-image-left__image {
          width: 420px;
        }
        .block-image-left__subimage {
          max-width: 244px;
          left: auto;
          right: -50px;
        }

        @media (max-width: 1320px) {
          .block-image-left__image {
            width: 480px;
          }
          .block-image-left__subimage {
            max-width: 190px;
          }
          .block-image-left_quote {
            max-width: 450px;
          }
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-image-left {
            flex-direction: column-reverse;
            margin-bottom: 80px;
            padding-bottom: 60px;
          }
          .block-image-left__image {
            width: 520px;
          }
          .block-image-left .app__image-group {
            top: 0;
            margin-top: 80px;
            right: 0;
          }
          .block-image-left_quote {
            max-width: initial;
          }
          .block-image-left_text {
            margin-left: 0;
          }
        }

        @media (max-width: ${screen.mobileMax}px) {
          .block-image-left__image {
            width: 240px;
          }
          .block-image-left {
            padding-bottom: 40px;
          }
          .block-image-left .app__image-group {
            margin-bottom: 40px;
          }
          .block-image-left_quote.app__desc {
            font-size: 28px;
            line-height: 1.29;
            letter-spacing: 0px;
          }
        }
      `}</style>
    </div>
  );
}
