import React from 'react';
import Img from 'gatsby-image';

import { screen } from '../constants';
import Button from './Button';
import { navigate } from 'gatsby';

export default function BlockImageRight({
  pretitle,
  title,
  description,
  quote,
  author,
  image,
  image_alt,
  sub_image,
  sub_image_alt,
  ctaText,
  ctaButtonTo,
}) {
  function handleCtaButtonTo() {
    navigate(ctaButtonTo);
  }
  return (
    <div className="block-image-right">
      <div className="block-image-right_text">
        <div className="app__pretitle">{pretitle}</div>
        <h2 className="app__title">{title}</h2>
        <div className="block-image-right_desc">{description}</div>
        {quote && <p className="app__desc block-image-right_quote">{quote}</p>}
        {author && <p className="block-image-right_author">{author}</p>}
        {ctaButtonTo && (
          <Button large onClick={handleCtaButtonTo}>
            {ctaText}
          </Button>
        )}
      </div>
      <div className="app__image-group">
        <div className="app__image block-image-right_image">
          <Img fluid={image} alt={image_alt} />
        </div>
        {sub_image && (
          <div className="app__subimage block-image-right_subimage">
            <Img fluid={sub_image} alt={sub_image_alt} />
          </div>
        )}
      </div>
      <style jsx>{`
        .block-image-right {
          display: flex;
          justify-content: space-between;
          align-items: flex-start;
          margin-top: 64px;
        }

        .block-image-right_desc {
          font-size: 24px;
          line-height: 1.42;
          letter-spacing: 0.9px;
          margin-bottom: 45px;
        }

        .block-image-right_text {
          max-width: 560px;
        }

        .block-image-right_quote {
          max-width: 500px;
          font-size: 31px;
          line-height: 1.29;
          letter-spacing: 0px;
          margin-bottom: 45px;
        }

        .block-image-right_author {
          font-size: 22px;
          line-height: 1.64;
          letter-spacing: 0.8px;
        }

        .block-image-right_image {
          width: 420px;
        }
        .block-image-right_subimage {
          max-width: 244px;
        }

        @media (max-width: 1140px) {
          .block-image-right_image {
            width: 340px;
          }
          .block-image-right_subimage {
            max-width: 190px;
          }
          .block-image-right_desc {
            max-width: 450px;
          }
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-image-right {
            flex-direction: column-reverse;
            margin-top: 80px;
            padding-bottom: 60px;
          }
          .block-image-right_image {
            width: 480px;
            height: 420px;
            overflow: hidden;
          }
          .block-image-right .app__image-group {
            margin-bottom: 80px;
          }
          .block-image-right_desc {
            max-width: initial;
          }
        }

        @media (max-width: ${screen.mobileMax}px) {
          .block-image-right_desc.app__desc {
            font-size: 28px;
            line-height: 1.29;
            letter-spacing: 0px;
          }
          .block-image-right_image {
            width: 220px;
            height: 235px;
          }
          .block-image-right .app__image-group {
            margin-bottom: 40px;
          }
          .block-image-right {
            margin-top: 40px;
          }
        }
      `}</style>
    </div>
  );
}
