import React from 'react';
import { navigate } from 'gatsby';

import Button from './Button';
import GetDemoButton from './GetDemoButton';

import { screen } from '../constants';

export default function BlockHeaderNoImage({
  title = '',
  desc = '',
  subtitle = '',
  secondaryButtonText = '',
  secondaryButtonTo = '/',
  primaryButtonText = '',
  primaryButtonTo = '/',
  maxWidth = '800',
}) {
  function handleSecondaryButtonText() {
    navigate(secondaryButtonTo);
  }

  return (
    <section className="block-header-no-image">
      <div className="block-header-no-image__content">
        <div className="block-header-no-image__subtitle app__pretitle">
          {subtitle}
        </div>
        <h1 className="app__title">{title}</h1>
        <p className="app__desc">{desc}</p>
        <div className="app__actions">
          {primaryButtonText && <GetDemoButton text={primaryButtonText} />}
          {secondaryButtonText && (
            <Button large onClick={handleSecondaryButtonText}>
              {secondaryButtonText}
            </Button>
          )}
        </div>
      </div>
      <style jsx>{`
        .block-header-no-image__content {
          text-align: center;
          margin-top: 104px;
          padding-bottom: 50px;
        }
        .block-header-no-image__content .app__desc,
        .block-header-no-image__content .app__title {
          max-width: ${maxWidth}px;
          margin-right: auto;
          margin-left: auto;
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-header-no-image__content {
            flex-direction: column-reverse;
          }
        }
      `}</style>
    </section>
  );
}
